import Vue from 'vue'
import router from './router'
import store from './vuex/store'
import App from './App'

require('assets/js/jquery.ta')

FastClick.attach(document.body)

// 
router.beforeEach((to, from, next) => {
	$.AMUI.progress.start();
	if(store.getters.getLogin.status) { // 
		let path = to.path.split('/')[1]; // 
		if(!path) { // 
			router.replace('/home')
		}else {
			path = '/' + path;
			// 
			store.getters.getSidebar.forEach(function(nav) {
				nav.active = false;
				if(nav.link == path) {
					nav.active = true;
				}
				if(nav.child) {
					nav.child.forEach(function(child) {
						child.active = false;
						if(child.link == to.path) {
							nav.active = true;
							child.active = true;
						}
					})
				}
			})
		}
	} else {
		if(to.path != '/login') {
			router.push('/login')
		}
	}
	next()
})

router.afterEach((to, from) => {
	// 
	if(store.getters.getLogin.status && to.path == '/login') {
		layer.msg('')
		router.replace(from.path)
	}
	$.AMUI.progress.done();
})

new Vue({
	router,
	store,
	el: '#app',
	template: '<App/>',
	components: {
		App
	},
	created() {
		const self = this;
		let path = self.$route.path.split('/')[1];
		if(path) {
			// 
			self.$router.replace('/' + path)
		}
		$.AMUI.progress.start()
	},
	mounted() {
		//  load
		setTimeout(function() {
			$.AMUI.progress.done(true);
			$('.ta-loader').addClass('animated fadeOut').one('webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend', function() {
				$(this).remove();
			});
		}, 50);
	}
}).$mount('#app')


